@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

$blue: #395178;
$orange: #e69171;
$grey: #808080;
$dark-grey: #808080;
$background: #fafafa;
$text: "Montserrat", sans-serif;

* {
  font-family: $text !important;
}

canvas {
  height: 120px;
}


.content_text {
  margin: auto;
}

.navigation-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
}

.profile-component {
  margin-top: 86px;
  margin-left: 256px;
  width: 100%;
  height: fit-content;
  background-color: $background;
}

.warningWrapper {
  height: 28px;
  width: 100px;
  overflow: hidden;
  margin-left: -40px;;
}

.profile_text {
  padding-top: 25px;
  width: 300px;
}

.c-profile-form {
  margin: auto;
  width: 300px;
}

.ant-form-item-control-input{
  height: 32px;
}
.c-profile-form label {
  font-size: 10px;
  color: rgba(0, 0, 0, 0.65);
  display: block;
  width: 300px;
  text-align: left;
}


.content {
  margin: auto;
  margin-top: 4%;
  width: 42em;
  margin-bottom: 4%;
  height: 44em;
  background-color: white;
  -webkit-box-shadow: 1px 1px 6px 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 1px 1px 6px 0 rgba(0, 0, 0, 0.1);
  box-shadow: 1px 1px 6px 0 rgba(0, 0, 0, 0.1);
}

.input_field {
  border-top: thin;
  color: red($color: #000000);
}

.inpBox {
  border-top: thin;
  border-left: thin;
  border-right: thin;
}
.input{
  padding-bottom: 10px;
}

.ant-input-affix-wrapper:focus, .ant-input-affix-wrapper-focused {
  input.ant-input {
    color: black;
  }
  color: black;
  box-shadow: none;
  border-color: black;
}

.ant-input-affix-wrapper:hover {
  border-color: grey;
}

.ant-input-affix-wrapper-lg {
  font-size: 15px;
}

.title {
  font-size: 10px;
}

.line {
  float: left;
}

.button_wrapper {
  justify-content: space-between;
  padding-top: 2.5%;
  display: flex;
  height: 32px;
  margin: auto;
  width: 53%;
  float: right;
}


.button_small:hover {
  color: white;
  background-color: #293d5f;
}

.avatar_img {
  outline-offset: -85px;
  outline: 85px rgba(0, 0, 0, 0.4) dotted;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  object-fit: cover;
}

.change_avatar {
  position: relative;
  margin: auto;
  width: 85%;
  height: 85%;
}

.edit-image-icon {
  color: white;
  top: 60%;
  left: 60%;
  font-size: large;
  position: absolute;
}

.profile_avatar_wrapper {
  padding: 25px;
  width: 100px;
  height: 100px;
  overflow: hidden;
}

.profile_avatar {
  width: 35px;
}


.change_window {
  background-color: rgba(148, 146, 146, 0.766);
  position: absolute;
  left: 48%;
  top: 38%;
  width: 350px;
  height: 240px;
}

.file_avatar_wrapper {
  justify-content: center;
  display: flex;
}

#avatar_text {
  padding-left: 32px;
  color: rgba(248, 248, 248, 0.835);
  text-shadow: black 2px 2px;
}

#success_img{
  margin: auto;
  position: relative;
  top: 25%;
  width: 120px;
}

.success{
  position: absolute;
  left: 45%;
  top: 20%;
  text-align: center;
  color: green;
  font-size: 28px;
}

.header_green{
  color: green;
  font-size: 1.2em;
}

@media only screen and (max-width: 1000px) {
  .ant-col:first-child {
    display: none;
  }
}
