@import '~antd/dist/antd.css';
:root{
    --picture-size: 35%;
}
h1{
    top: 10%;
    font-size: 3.5em;
    white-space: nowrap;
}
h2{
    text-align: left;
    font-size: 2em;
    color: grey;
}

/*.addPadding{*/
/*    padding-top: 15px;*/
/*}*/

.button{
    min-width: 150px;
    min-height: 50px;
    display: inline;
}

.mainframe{
    background-color: aliceblue;
}
.buttonsWrapper .ant-btn-primary{
    background-color: #395178;
    border-color: #395178;
}
.warningWrapper{
    min-width: 380px;
    text-align: center;
}
#warning{
    color: red;
}

.leftPicture{
    opacity: 0.7;
    background: url(./login_background.jpg) no-repeat center center;
    background-size: cover;
    position: absolute;
    left: 0;
    width: var(--picture-size);
    height: 100%;
    overflow: hidden;
    display: flex;

}

.mainContainer{
    height: 100%;
    width: calc(100% - var(--picture-size));
    position: absolute;
    text-align: center;
    right: 0;
    overflow: hidden;

}

.preLogo{
    width: 100%;
    height: 24%;
    /* justify-content: space-between; */
    margin: auto;
}



.centerBox{
    width: fit-content;
    min-width: 500px;
    text-align: left;
    margin:auto;
}

.textColorBlue{
    color: #192F66;
}
.textColorBrown{
    color: #e69171;
}
.headerPlaceholder{
    height: 12%;
}


.head2Wrapper{
    font-size: 13px;
}

.credentialsWrapper{
    margin-top: 4%;
    width: fit-content;
    overflow: visible;
    min-width: 100px;
    max-width: 300px;
    margin-left: 11%;
 }

 .buttonsWrapper{
     padding-top: 50px;
     width: 390px;
     margin-left: 5%;
 }

.floatLeft{
    float: left;
}

.floatRight{
    border-color: #395178;
    color: #395178;
    float: right;
}
.buttonsWrapper .floatRight:hover{
    background-color: #395178;
    color: white;
}
.col {
    display: block;
}

.passOptions{
    justify-content: space-between;
    display: flex;
    height: fit-content;
    width: 95%;
    min-width: 60px;
    margin-left: auto;
    margin-top: -2%;
}

.forgotPassword{
    color: darkgrey;
    font-size: 13px;
}
.rememberMe{
    color: darkgrey;
    font-size: 13px;
}

.inpBox{
    border-radius: 0em;
    margin-bottom: 6%;
}

.inpBot{
    border-top: thin;
}

.logoSign {
    margin-left: 42.5%;
    background: url(/static/media/act_logo.a3026ef0.png) no-repeat center center;
    background-size: contain;
    width: 140px;
    height: 86px;
    padding-left: 20px;
    opacity: 0.9;
}

.ant-input-affix-wrapper > input.ant-input {
    padding: 4px;
    border: none;
    outline: none;
}

.reg_modal{
    min-width: 420px;
    height: 820px;
}

.input_wrapper{
    height: 100px;
}

#reg_warning{
    color: red;
    margin: auto;
    display: block;
    width: 400px;
    height: 50px;
    text-align: center;
}

