@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
    font-family: "Montserrat", sans-serif !important;
}

.service-container {
    width: 100%;
    height: 100%;
    margin-left: 256px;
    margin-top: 86px;
    background-color: #fafafa;
}

.service-block {
    padding: 4% 12%;
    margin: auto;
    margin-top: 3%;
    width: fit-content;
    height: 36em;
    background-color: white;
    box-shadow: 1px 1px 6px 0 rgba(0, 0, 0, 0.1);
}

.c-service-form {
    margin: auto;
    width: fit-content;
}

.c-service-form .ant-form-item {
    display: block;
    width: 24em;
    margin: auto;
    margin-bottom: 3em;
}

.ant-form-item-label > label::after {
    content: '';
}

.ant-form-item-label > label.ant-form-item-required::before {
    content: '';
}

.c-service-form .ant-form-item-label > label {
    font-size: 10px;
    color: rgba(0, 0, 0, 0.65)
}

.ant-input, .ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    font-size: 14px;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    border-radius: 0;
}

.ant-input:focus, .ant-input-focused, .ant-select-focused.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    color: black;
    box-shadow: none;
    border-color: black;
}

.ant-input:focus, .ant-input-focused input.ant-input {
    color: black;
}

.ant-input:hover {
    border-color: grey;
}

.ant-form-item .ant-mentions, .ant-form-item textarea.ant-input {
    margin-top: 3em;
    border-left: 1px solid #d9d9d9;
    border-right: 1px solid #d9d9d9;
    border-top: 1px solid #d9d9d9;
    border-radius: 0;
}

.ant-form-item .ant-mentions, .ant-form-item textarea.ant-input:focus {
    border-color: black;
}

.ant-form-item .ant-mentions, .ant-form-item textarea.ant-input:hover {
    border-color: grey;
}


.ant-input-affix-wrapper{
    font-size: 15px;
    border-left: 0;
    border-right: 0;
    border-top: 0;
    border-radius: 0;
}
.ant-input-affix-wrapper:focus, .ant-input-affix-wrapper-focused {
    color: black;
    box-shadow: none;
    border-color: black;
}

.ant-input-affix-wrapper:hover {
    border-color: grey;
}

.ant-input-affix-wrapper:focus, .ant-input-affix-wrapper-focused input.ant-input {
    color: black;
}
.c-service-form .ant-btn-primary{
    float: right;
}
.ant-input[disabled]{
    color: black;
}
.c-service-form .button_wrapper {
    padding-top: 4em;
    justify-content: space-between;
    display: flex;
    height: 32px;
    margin: auto;
    width: 12em;
}

.input_service{
    height: 7em;
}