@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

$blue: #395178;
$blue-opacify: #39517814;
$orange: #e69171;
$grey: #808080;
$dark-grey: #808080;
$background: #fafafa;
$text: "Montserrat", sans-serif;

* {
    font-family: $text !important;
    box-sizing: inherit;
    &:before,
    &:after {
        box-sizing: inherit;
    }
}

.side-menu-container .ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
    background-color: $blue-opacify;
}
.side-menu-container .ant-menu {
    margin-top: 60px;
}
.side-menu-container .ant-menu-item {
    margin-bottom: 16px !important;
    padding-left: 32px !important;
}
.side-menu-container .ant-menu-inline > .ant-menu-item {
    height: 52px !important;
    line-height: 52px !important;
}
.logo {
    margin-left: 22px;
    background: url("../signin/act_logo.png") no-repeat center center;
    background-size: contain;
    width: 140px;
    height: 86px;
    opacity: 0.95;
}
.header-container {
    z-index: 1000;
    position: fixed;
    margin-left: 256px;
    width: calc(100% - 256px);
}
.header-container .ant-card-meta-avatar {
    padding: 0 !important;
}
.header-container .ant-avatar {
    background: url("../header/profile.jpg") no-repeat center center !important;
    background-size: cover !important;
    width: 45px !important;
    height: 45px !important;
}
.header-container .ant-card-meta-description {
    color: $grey !important;
    font-weight: 500;
}
.header-container .ant-card-meta-detail {
    align-items: center;
    display: flex;
}
side-menu-container .ant-layout-sider {
    max-width: 0 !important;
    min-width: 300px !important;
    width: 100% !important;
}

.header-container .ant-card {
    border-radius: 0px;
}
.header-container .ant-card-meta {
    display: flex;
    margin: 0 !important;
}
.header-container .ant-card-body {
    padding: 0 !important;
}

.header-container .ant-card-bordered {
    border: 0 !important;
}
.side-bar {
    z-index: 2;
    height: 100vh;
    position: fixed;
    width: 256px;
    box-shadow: 1.5px 1px 3px 0px rgba(0, 0, 0, 0.1);
}

.divider {
    margin-top: 12%;
    margin-bottom: 20%;
    width: 82%;
    border: 0.5px $blue-opacify solid;
}

.header-container .ant-page-header-ghost {
    background-color: white !important;
    box-shadow: 1px 1.5px 3px 0px rgba(0, 0, 0, 0.1);
}

.header-container .ant-page-header-heading-extra {
    width: 35%;
    justify-content: space-evenly;
    display: flex;
}
.header-container .ant-input-group-wrapper {
    width: 80%;
    margin: auto;
}
.header-container .ant-page-header-heading-left {
    padding-left: 4%;
}

.header-alert {
    background-color: white;
    box-shadow: 0px 0px 3px 0px rgba(0, 0, 0, 0.15);
    border-radius: 23px;
    padding: 11px;
    justify-content: center;
    margin: 0 !important;
    width: fit-content;
    height: 46px;
    //z-index: 2;
    display: flex;
}

.search {
    display: table;
    input {
        background: none;
        border: none;
        outline: none;
        width: 26px;
        height: 23px;
        min-width: 0;
        padding: 0;
        z-index: 1;
        position: relative;
        font-size: 14px;
        -webkit-appearance: none;
        transition: all 0.6s ease;
        cursor: pointer;
        color: $grey;
        & + div {
            position: relative;
            height: 28px;
            width: 100%;
            margin: -22px 0 0 0;
            svg {
                display: block;
                position: absolute;
                height: 24px;
                width: 162px;
                right: -7px;
                top: 0;
                fill: none;
                stroke: $grey;
                stroke-width: 1.2px;
                stroke-dashoffset: 212.908 + 59;
                stroke-dasharray: 59 212.908;
                transition: all 0.6s ease;
            }
        }
        &:not(:placeholder-shown),
        &:focus {
            width: 146px;
            padding: 0 4px;
            cursor: text;
            & + div {
                svg {
                    stroke-dasharray: 150 212.908;
                    stroke-dashoffset: 300;
                    stroke-width: 0.5px;
                }
            }
        }
    }
}

::selection {
    background: rgba(#fff, 0.2);
}
::-moz-selection {
    background: rgba(#fff, 0.2);
}

@media only screen and (max-width: 1000px) {
    .notification-icon {
        width: 32px !important;
        height: 36px !important;
        stroke: $grey;
        fill: none;
        stroke-width: 2px !important;
    }
    .navigation-wrapper {
        display: block !important;
    }
    .mobile-header .ant-page-header {
        padding: 42px 32px 52px 32px !important;
    }
    .mobile-header .ant-page-header-heading-left {
        margin: auto !important;
        padding: 0 !important;
    }
    .mobile-header .ant-page-header-heading-extra {
       width: 100% !important;
    }

