.mobile-header {
    width: 100%;
}
.header-profile {
    z-index: 2;
    background: url("../header/profile.jpg") no-repeat center center;
    background-size: cover;
    width: 2em;
    height: 2em;
    border-radius: 50%;
    float: right;
}

.mobile-footer {
    display: flex;
    justify-content: space-around;
    position: fixed;
    bottom: 0;
    z-index: 3;
    width: 100%;
    height: 8em;
    background-color: white;
    -webkit-box-shadow: 0px -2px 4px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 0px -2px 4px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 0px -2px 4px 0px rgba(0, 0, 0, 0.1);
}
.mobile-footer svg {
    width: 2em;
    height: 2em;
    path {
        fill: $grey;
    }

    cursor: pointer;
}
.mobile-footer .active svg {
    width: 2em;
    height: 2em;
    path {
        fill: $blue;
    }

    cursor: pointer;
}

.footer-icon {
    display: inline-flex;
    width: fit-content;
}

.footer-icon-block {
    display: flex;
    justify-content: space-around;
    width: 100%;
    margin-top: 42px;
}

.notification-icon {
    z-index: 10;
    width: 24px;
    height: 32.5px;
    fill: $grey;
    stroke-width: 1.8px;
}
svg:hover {
    cursor: pointer;
}
#notification-alert {
    fill: rgba(204, 50, 3, 0.99);
}
.vertical-divider {
    margin: 0 !important;
    border: 0.5px $blue-opacify solid;
}
.navigation-wrapper {
    display: flex;
}
