@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

$blue: #395178;
$orange: #e69171;
$grey: #808080;
$dark-grey: #808080;
$background: #f6f7f7;
$text: "Montserrat", sans-serif;

* {
  font-family: $text !important;
}

h1,
h2,
h3,
h4 {
  margin: 0;
}

.dashboard-container {
    margin-top: 86px;
  margin-left: 256px;
  width: 100%;
  height: 100%;
  background-color: $background;
}
.dashboard-block {
  justify-content: space-around;
  padding: 2%;
  display: flex;
}
.ant-btn-primary{
  background-color: $blue;
  color: white;
}
.ant-btn-primary:hover{
  background-color: #293d5f;
}
.content-block {
  padding: 2% 3.5%;
  background-color: white;
  -webkit-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
  box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);

  h3 {
    //position: absolute;
    width: fit-content;
  }
}
.ant-slider-mark-text:last-child{
  left: 97.5% !important;
}
.devices-table {
  width: 100%;
}

.map-box {
  height: 100%;
  margin: auto;
}
.webmap {
  //height: 40em;
  padding: 0;
  margin: 0;
  height: 100%;
}
.mp{
  padding: 2% !important;
}
.dashboard-container li .ant-pagination-prev,
.ant-pagination-next,
.ant-pagination-jump-prev,
.ant-pagination-jump-next,
.ant-pagination-item,
.ant-pagination-disabled {
  min-width: 24px;
  height: 24px;
  line-height: 24px;
  color: $grey;
}

.dashboard-container .ant-pagination-item-active {
  border-color: $grey;
}

.dashboard-container .ant-pagination-item-active a {
  color: $blue;
}

.esri-view .esri-view-surface--inset-outline:focus::after {
  outline: none !important;
}

@media only screen and (max-width: 600px) {
  //.graphContainer {
  //  width: 80vw;
  //}
}

.dashboard-container .ant-card-body {
  padding: 6% 0 6% 8%;
}

@media only screen and (max-width: 1000px) {
  .dashboard-container .ant-col:first-child {
    display: none;
  }
}
.dashboard-container .ant-col-12{
  padding: 0 !important;
}
.dashboard-container .ant-row{
  margin: 0 !important;
}
.statistics-container{
  padding-top: 1.5%;
  flex-wrap: wrap;
  display: flex;
  width: 100%;
  height: 100%;
  justify-content: space-evenly;

  .ant-card-bordered {
    height: fit-content;
    border: 1px rgba(169, 169, 169, 0.3) solid !important;
    width: 42% !important;
    padding: 4% 0 2% 4%;
  }
}
.ant-slider-with-marks{
  margin-bottom: 0;
}
.dashboard-container .ant-statistic-content{
  font-size: 32px;
}
.dashboard-container .ant-statistic-title{
  font-size: 18px;
  margin-bottom: 10px;
}
