@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

* {
    font-family: "Montserrat", sans-serif !important;
}

.notification-container {
    padding: 1.5%;
    margin-top: 86px;
    margin-left: 256px;
    width: 100%;
    height: fit-content;
    background-color: #f6f7f7;
}

.devices-table {
    width: 100%;
}

.notifications-block {
    flex-flow: column;
    padding: 2% 8%;
    justify-content: space-around;
    display: flex;
    width: 70%;
    margin: auto;
    height: fit-content;
    background-color: white;
    -webkit-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
    -moz-box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
    box-shadow: 3px 3px 4px 0px rgba(0, 0, 0, 0.1);
}

.table-row-light {
    background-color: #ffffff;
}
.table-row-warning {
  background-color: rgb(255, 248, 235);
}
.table-row-warning:hover > td {
  background: rgb(255, 248, 235) !important;
}
.table-row-alert {
  background-color: rgb(255, 234, 234);
}
.table-row-alert:hover > td {
  background: rgb(255, 234, 234) !important;
}
.table-row-dark {
    background-color: #f7f7f7;
}

.table-row-dark:hover > td {
    background:  #f7f7f7 !important;
}

.ant-table-thead > tr > th {
    background: rgba(241, 245, 252, 0.75);
}
.filters-block{
    width: 100%;
    display: flex;
    justify-content: flex-start;
    padding: 4% 0;
    align-items: center;
}
.filters-block label{
    margin-right: 3%;
}
.filters-block .ant-checkbox-group{
    width: 40%;
}
